import React, { Suspense } from 'react';
import "./App.css";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import OffersControl from './Dashboard/Page/OffersControl';
import ServicesControl from './Dashboard/Page/ServicesControl';
import GuideControl from './Dashboard/Page/GuideControl';

// Lazy imports
const Home = React.lazy(() => import("./Home/Page/Home"));
const Navbar = React.lazy(() => import("./Shared/Components/Sections/Navbar"));
const UseSection = React.lazy(() => import("./Shared/Components/Sections/UseSection"));
const Services = React.lazy(() => import("./Services/Page/Services"));
const Footer = React.lazy(() => import("./Shared/Components/Sections/Footer"));
const FQSection = React.lazy(() => import("./Shared/Components/Sections/FQSection"));
const Guide = React.lazy(() => import("./Guide/Page/Guide"));
const GuideUsing = React.lazy(() => import("./Guide/Components/GuideUsing"));
const Support = React.lazy(() => import("./Support/Page/Support"));
const Offers = React.lazy(() => import("./Offers/Page/Offers"));
const Login = React.lazy(() => import("./Login/Pages/Login"));
const HomeDashboard = React.lazy(() => import("./Dashboard/Page/HomeDashboard"));
const UsersDashBoard = React.lazy(() => import("./Dashboard/Page/UsersDashBoard"));
const HomeControl = React.lazy(() => import("./Dashboard/Page/HomeControl"));
const AddUserForm = React.lazy(() => import("./Dashboard/Components/AddUserForm"));
const Chat = React.lazy(() => import("./Dashboard/Page/Chat"));
const AdminChat = React.lazy(() => import("./Dashboard/Page/AdminChat"));
const Redirect = React.lazy(() => import("./Shared/Components/Elements/Redirect"));

const MainLayout = ({ children }: any) => (
  <>
    <Navbar />
    <div style={{ overflowX: "hidden" }}>{children}</div>
    <FQSection className="FQSection" />
    <Chat />
    <Footer />
  </>
);

const DashboardLayout = ({ children }: any) => (
  <Suspense fallback={<div></div>}>{children}</Suspense>
);

function App() {
  return (
    <Router>
      <Routes>
        {/*     ( Navbar  Footer) */}
        <Route element={<Redirect />}>
          <Route
            path="dashboard"
            element={
              <DashboardLayout>
                <HomeDashboard />
              </DashboardLayout>
            }
          >
            <Route path="" element={<UsersDashBoard />} >
              <Route path="add" element={<AddUserForm />} />
            </Route>
            <Route path="support" element={<AdminChat />} />
            <Route path="home" element={<HomeControl />} />
            <Route path="offers" element={<OffersControl />} />
            <Route path="services" element={<ServicesControl />} />
            <Route path="guide" element={<GuideControl />} />
          </Route>
        </Route>

        {/*     ( Navbar  Footer) */}
        <Route
          path="/"
          element={
            <MainLayout>
              <Home />
            </MainLayout>
          }
        >
          <Route index element={<UseSection />} />
          <Route path=":id" element={<UseSection />} />
        </Route>
        <Route
          path="home"
          element={
            <MainLayout>
              <Home />
            </MainLayout>
          }
        >
          <Route index element={<UseSection />} />
          <Route path=":id" element={<UseSection />} />
        </Route>

        <Route
          path="offers"
          element={
            <MainLayout>
              <Offers />
            </MainLayout>
          }
        />

        <Route
          path="services"
          element={
            <MainLayout>
              <Services />
            </MainLayout>
          }
        />

        <Route
          path="guide"
          element={
            <MainLayout>
              <Guide />
            </MainLayout>
          }
        >
          <Route index element={<GuideUsing />} />
          <Route path=":id" element={<GuideUsing />} />
        </Route>

        <Route
          path="support"
          element={
            <MainLayout>
              <Support />
            </MainLayout>
          }
        />

        <Route
          path="login"
          element={
            <MainLayout>
              <Login />
            </MainLayout>
          }
        />
      </Routes>
    </Router>
  );
}

export default App;
